import React, { Component } from "react";
import "./index.css";
import todosData from "./todoData";
import Todoitem from "./Todoitem";

export default class App extends Component {
  constructor() {
    super();

    this.state = {
      todoObj: todosData,
    };
    this.handleChange = this.handleChange.bind(this);
  }

  handleChange = (id) => {
    this.setState((previousState) => {
      const updatedTodoObject = previousState.todoObj.map((data) => {
        if (data.id === id) {
          data.completed = !data.completed;
        }
        return data;
      });
      return {
        todoObj: updatedTodoObject,
      };
    });
  };

  render() {
    const todoItems = this.state.todoObj.map((data) => (
      <Todoitem key={data.id} item={data} handleChange={this.handleChange} />
    ));
    return <div className="mainDiv">{todoItems}</div>;
  }
}
